<template>
    <div>
        <ul>
            <div class="mission">
                <li v-for="(item, index) in items" :key="index">
                    <div class="items">{{ item.text }}</div>
                    <div v-if="item.status" class="status-true" @click="updateClass(index)">已達成</div>
                    <div v-else class="status-false" @click="updateClass(index)">未達成</div>
                    <div class="delete" @click="deleteItems(index)">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 29 29" width="24px" height="24px">
                            <path fill-rule="evenodd" d="M 7 7 L 9 5 L 14 10 L 19 5 L 21 7 L 16 12 L 21 17 L 19 19 L 14 14 L 9 19 L 7 17 L 12 12 Z"/>
                        </svg>
                    </div>
                </li>
            </div>
        </ul>     
    </div>
</template>
<script setup>
    import { reactive, defineProps, watch } from 'vue';

    const items = reactive([])
    items.push({text: 'ABC', status: false})

    const props = defineProps(['textProp'])

    const update = () => {
        items.push(
            {
                text: props.textProp[props.textProp.length - 1],
                status: false,
            }
        )
    }

    const updateClass = (index) => {
        // console.log(index, items[index]);
        items[index].status = !items[index].status
    }

    const deleteItems = (index) => {
        items.splice(index, 1)
    }
    deleteItems()

    watch(props.textProp, () => {
            update()
            // console.log(props.textProp);
        },
        { 
            deep: true 
        }
    )



</script>
<style>
    .items , .status-true , .status-false {
        display: inline-flex;
    }
    .items {
        font-size: 23px;
    }
    .mission li {
        position: relative;
        text-align: right;
        font-family: '微軟正黑體';
    }
    .mission {
        position: relative;
        /* border: 1px solid; */
        width: 1031px;
    }
    ul {
        list-style: none;
    }
    .status-true , .status-false {
        position: relative;
        border-radius: 3px;
        font-size: 16px;
        margin: 0px 8px 0px 10px;
        padding: 0px 2px;
        bottom: 3px;
        cursor: pointer;
    }
    .status-false {
        color: red;
        background-color: #FFD2D2;
        border: red  1px solid;
    }
    .status-true {
        color: green;
        background-color: 	#DFFFDF;
        border: green 1px solid;
    }
    .delete {
        display: inline-flex;
        position: relative;
        margin-left: 5px;
        border: 1px solid red;
        width: 22px;
        height: 22px;
        background-color: red;
        top: 3px;
        border-radius: 4px;
        cursor: pointer;
    }
    .delete svg {
        position: relative;
        fill: white;
        left: 1px;
        top: 1px;
    }
</style>