<template>
    <div>
        <p>任務管理器 (To-Do List)</p>
        <div class="add-div">
            <input type="text" placeholder="在此新增任務" v-model="text" @keyup.enter="add()">
            <div class="add" @click="add()">
                <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 29 29" width="24px" height="24px"><path fill-rule="evenodd" d="M 11 2 L 11 11 L 2 11 L 2 13 L 11 13 L 11 22 L 13 22 L 13 13 L 22 13 L 22 11 L 13 11 L 13 2 Z"/></svg>
            </div>
        </div>
        
        <ToDoItem :textProp="textProp"/>
    </div>
</template>
<script>
    import{ reactive, toRefs } from 'vue'
    import ToDoItem from './ToDoItem.vue'

    export default {
        name: 'toDoList',
        components: {
            ToDoItem,
        },
        setup() {
            const user = reactive({
                id: 0,
                text: '',
            })
            let textProp = reactive([])
            const add = () => {
                if( user.text !== '' & user.text.split(' ').join('').length !==0) {
                    textProp.push(user.text)
                    if(textProp.length > 1) {
                        textProp.splice(0, 1)
                    }
                }
                user.text = ''
                // console.log(textProp);
            }
            return {
                ...toRefs(user),
                textProp,
                add,
            }
        },
    }
</script>
<style>
    .add {
        display: inline-flex;
        position: relative;
        margin-left: 5px;
        border: 1px solid #42b983;
        width: 23px;
        height: 23px;
        background-color: #42b983;
        top: 8px;
        border-radius: 4px;
        cursor: pointer;
    }
    input {
        width: 200px;
        height: 20px;
        margin-bottom: 5px;
    }
    .add svg {
        /* display: inline-flex; */
        position: relative;
        left: 2px;
        top: 2px;
        fill: white;
    }
    .add-div {
        position: relative;
    }
    p {
        font-size: 24px;
    }
</style>